import * as config from './config.js'
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: config.baseUrl
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		const token = uni.getStorageSync('token');
		const tokenHead = uni.getStorageSync('tokenHead');
		if (token && tokenHead) {
			config.header.Authorization = tokenHead + token;
		}
		config.header['Content-Type'] = 'application/x-www-form-urlencoded'
		return config;
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// console.log(res);
		// originalDatatrue
		// res.statueCoderes.data.code
		let timer = ''
		if (res.code == 200) {
			// originalDatatruereturnthis.$u.postthen
			return res.data;
		} else if (res.code == 401) {
			uni.showToast({
				title: "",
				icon: "none",
				duration: 1500,
				mask: true,
				success: function() {
					if(!timer){
						timer = setTimeout(function(){
							uni.clearStorage()
							uni.navigateTo({
								url: '/pages/login/login'
							})
							clearTimeout(timer)
						}, 1500)
					}
				}
			})
			// token 
			// let http = vm.$u.http
			// // vm.$u[options.method](options.url,options.params)
			// let refreshTokenUrl = 'http://47.96.37.228:3001/auth/oauth/token'
			// vm.$u.post(refreshTokenUrl, {
			// 	grant_type: 'refresh_token',
			// 	client_id: 'client-app',
			// 	client_secret: '123456',
			// 	refresh_token: uni.getStorageSync('refreshToken')
			// }).then(res=>{
			// 	uni.setStorageSync('tokenHead', res.tokenHead)
			// 	uni.setStorageSync('token', res.token)
			// 	uni.setStorageSync('refreshToken', res.refreshToken)
			// 	console.log(http)
			// });h_token: uni.getStorageSync('refreshToken')
			// });

			// console.log(result)
		} else {
			if(res.code == 500 && res.message == '') {
				uni.showToast({
					title:'',
					icon: 'none'
				})
			}
			console.log(JSON.stringify(res));
			return res
		};
	}
}

export default {
	install
}
